<template>
  <view class="work-container">
	<uni-section v-if :title="workLogId ? '修改工作量' : '添加工作量'" type="line">
		<view>
			<uni-forms ref="form" :rules="rules" :modelValue="form">
				<uni-forms-item label="日期" required>
					<uni-datetime-picker type="date" v-model="form.workDay"/>
				</uni-forms-item>
				<uni-forms-item label="项目" required>
					<uni-data-select
						v-model="form.taskId"
						:text="form.taskName"
						:localdata="taskCols"
						@change="proChange"
				      ></uni-data-select>
				</uni-forms-item>
				<uni-forms-item label="类型" required>
					<uni-data-select
						v-model="form.workType"
						:localdata="workTypeCols"
						@change="wtChange"
				      ></uni-data-select>
				</uni-forms-item>
				<uni-forms-item label="单位" required>
					<uni-data-select
						v-model="form.measureUnit"
						:localdata="measureUnitCols"
				      ></uni-data-select>
				</uni-forms-item>
				<uni-forms-item label="数量" v-if="!showOtherCount" required>
					<uni-number-box v-model="form.amount" :step="1" :min="minValue" :max="maxValue" />
				</uni-forms-item>
				<uni-row v-if="showOtherCount">
					<uni-col :span="12">
						<uni-forms-item label="A4:" required>
							<uni-number-box v-model="form.amount4" :step="1" :min="minValue" :max="maxValue" />
						</uni-forms-item>
					</uni-col>
					<uni-col :span="12">
						<uni-forms-item label="A3:" required>
							<uni-number-box v-model="form.amount3" :step="1" :min="minValue" :max="maxValue" />
						</uni-forms-item>
					</uni-col>
				</uni-row>
				<uni-row v-if="showOtherCount">
					<uni-col :span="12">
						<uni-forms-item label="A2:" required>
							<uni-number-box v-model="form.amount2" :step="1" :min="minValue" :max="maxValue" />
						</uni-forms-item>
					</uni-col>
					<uni-col :span="12">
						<uni-forms-item label="A1:" required>
							<uni-number-box v-model="form.amount1" :step="1" :min="minValue" :max="maxValue"  />
						</uni-forms-item>
					</uni-col>
				</uni-row>
				<uni-row v-if="showOtherCount">
					<uni-col :span="12">
						<uni-forms-item label="A0:" required>
							<uni-number-box v-model="form.amount0" :step="1" :min="minValue" :max="maxValue" />
						</uni-forms-item>
					</uni-col>
				</uni-row>
				<uni-forms-item label="明细">
					<uni-easyinput type="textarea" v-model="form.workDetails" placeholder="请输入工作明细" />
				</uni-forms-item>
			</uni-forms>
		</view>
		<view class="button-group">
			<button v-if="workLogId"  style="color: #ffffff; background-color: #ff0000;"  size="mini" @click="del">删除</button>
		    <button v-else style="color: #000000; background-color: #ffffff;" size="mini" @click="goBack()">返回</button>
			<button type="primary" size="mini" @click="submit">提交</button>
		</view>
	</uni-section>
  </view>
</template>

<script>
import {getWorkLogById,workLogAdd,workLogModify} from '@/api/work/archive/worklog'
import {getTaskList} from '@/api/work/archive/task'
export default {
  data () {
    return {
		showOtherCount:false,//显示A0-A4页数
		nowDay: Number(new Date()),
		maxValue:200,
		minValue:0,
		workLogId: undefined,
		form: {
			workDay:'',
			taskId:'',
			taskName:'',
			amount: 0,
			workType: '',
			measureUnit: '',
			workDetails:'',
			amount0: 0,
			amount1: 0,
			amount2: 0,
			amount3: 0,
			amount4: 0,
		},
		taskCols: [],//项目列表
		measureUnitCols: [
				{
				  text: '卷',
				  value: '01'
				}, 
				{
				  text: '件',
				  value: '02'
				},
				{
				  text: '页',
				  value: '03'
				}
		],	
		workTypeCols:[
			{
			  text: '分卷',
			  value: '01'
			}, 
			{
			  text: '著录',
			  value: '02'
			},
			{
			  text: '敲页码',
			  value: '03'
			},
			{
			  text: '装订',
			  value: '04'
			},
			{
			  text: '折图',
			  value: '05'
			},
			{
			  text: '检查',
			  value: '06'
			},
			{
			  text: '扫描',
			  value: '07'
			},
		],
		rules: {
			workDay: {
				rules:[{
					required: true,
					errorMessage: '请选择日期',
				}],
				label:'日期',
				validateTrigger:'submit'
			},
			taskId: {
				rules:[{
					required: true,
					errorMessage: '请选择项目',
				}],
				label:'项目',
				validateTrigger:'submit'
			},
			workType: {
				rules:[{
					required: true,
					errorMessage: '请选择类型',
				}],
				label:'类型',
				validateTrigger:'submit'
			},
			measureUnit: {
				rules:[{
					required: true,
					errorMessage: '请选择单位',
				}],
				label:'单位',
				validateTrigger:'submit'
			}
		}
	}
  },
  onLoad (params) {
    this.workLogId = params.id
    this.loadData()
  },
  onReady() {
	// 需要在onReady中设置规则
	this.$refs.form.setRules(this.rules)
  },
  methods: {
	//wtChange
	wtChange(e){
		if(e == '07' || e == '05'){
			this.showOtherCount = true
		}else{
			this.showOtherCount = false
		}
	},
	//proChange
	proChange(e){
		// this.form.taskId = e.value[0].id;
		this.form.taskName = this.taskCols.projectName;
	},
	// 表单重置
	reset() {
	  this.form = {
		workDay:'',
		taskId:'',
		taskName:'',
		amount: 0,
		amount0: 0,
		amount1: 0,
		amount2: 0,
		amount3: 0,
		amount4: 0,
		workType: '',
		measureUnit: '',
		workDetails:''
	  };
	  this.showOtherCount = false;
	},
    loadData () {
		const app = this
		// 获取所有未完成任务列表
		this.$nextTick().then(() => {
			const param = {
				taskStatus:'01',//未完成状态
			}
			getTaskList(param).then(res => {
				app.taskCols = res.data
			})
		})

		if (this.workLogId) {
			const app = this
			getWorkLogById(this.workLogId).then(res => {
			  app.form = res.data
			  if(app.form.workType == '07' || app.form.workType == '05'){
			  	app.showOtherCount =true
			  }else{
			  	app.showOtherCount =false
			  }
			})
		}else{
			this.form.workDay = this.nowDay;
		}
    },
    del () {
		uni.showToast({ title: '请联系管理员进行删除！' })
    },
	submit () {
		const app = this
		this.$refs.form.validate().then(res => {
			if (app.workLogId) {
			  workLogModify(app.form).then(res => {
				uni.showToast({ title: '修改成功！' })
				app.reset();
				uni.navigateBack({ delta: 1});
			  })
			} else {
			  workLogAdd(app.form).then(res => {
				uni.showToast({ title: '提交成功！' })
				app.reset();
				uni.navigateBack({ delta: 1});
			  })
			}
		});
	},
    
    goBack () {
      uni.navigateBack({ delta: 1});
    }
  }
}
</script>

<style lang="scss">
.button-group {
	margin-top: 15px;
	display: flex;
	justify-content: space-around;
}

.form-item {
	display: flex;
	align-items: center;
}

.button {
	display: flex;
	align-items: center;
	height: 20px;
	margin-left: 10px;
}
</style>